.loading_page {
    margin: auto;
}
.layout {
    background-image: url('./dallasBackground.png');
}

/* Landing Page CSS */
.landing_grid {
    width: 50vw;
    height: 100vh;
}

.landing_cell {
    display:flex;
    flex-direction: column;
    display: flex;
}

.landing_img {
    max-width: 50%;
    margin-left: 12.5%;
    margin-right: 12.5%;
    height: auto;
    width: auto;
    padding: 0vw 10vw 0vw 10vw;
}

.landing_banner {
    background-color: black;
    opacity: .8;
    margin: auto;
    border-radius: 7%;
    width: 50vw;

}

.landing_banner h1 {
    text-align: center;
    font-size: 66px;
    color: white;
    font-weight: bold;
    font-family: 'Barlow Condensed';
}

.landing_banner hr {
    margin: auto;
    border-top: 5px dotted white;
    width: 60%;
}
.landing_banner p {
    margin: 5% 20% 5% 20%;
    color: white;
    font-family: 'Barlow Condensed';
    width: 60%;
    text-align: center;
    font-size: 20px;
    
}

.landing_socials {
    max-width: 50%;
    margin-left: 25%;
    font-size: 9vh;
    display: flex;
    justify-content: space-evenly;  
}

.landing_socials i{
    color:white;
}
.fa-linkedin-square {
    text-align: center;
}
.fa-github-square {
    text-align: center;
}



/* Project page CSS */
.projects-grid{
    display: flex;
}

/* Contact page */
.contact-body{
    margin: auto;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;

}
.contact__img{
    object-fit: contain;
}
.contact-grid{
    text-align: center;
    justify-content: center;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    background: rgb(75, 72, 72);
    color: whitesmoke;
}
.contact_left_cell{
    display: flex;
    flex-direction: column;
}

.contact_left_cell img {
    padding: 0% 13% 0% 13%;
    max-width: 50%;
    margin-left: 12.5%;
    margin-right: 12.5%;
    height: auto;
    width: auto;
}
.contact-grid h2{
    font-family: 'Barlow Condensed';
    color: whitesmoke;
    font-size: 2.75vw;
}

.contact-grid p{
    font-family: 'Barlow Condensed';
    color: whitesmoke;
    font-weight: bold;
}
.contact-grid hr{
    border-top: 5px dotted whitesmoke;
    width: 50%;
    margin: auto;
}
.contact-list i{
    font-size: 50px;
    padding-right: 5rem;
    color:whitesmoke;

}



/* Resume CSS */
.resume-left-col {
    background: #c7c2c2;
    opacity: 0.8;
}
.resume-right-col {
    background: #554f4b;
    opacity: 0.8;
    color: white;
    padding: 2em;
} 
.resume-left-col h2 {
    font-size: 2.75vw;
    font-family: 'Barlow Condensed';
}
.resume-left-col h4 {
    font-size: 1.75vw;
    font-family: 'Barlow Condensed';
}
.resume-left-col h3 {
    font-size: 2.8 vw;
    font-family: 'Barlow Condensed';
}
.resume-left-col p {
    font-size: 1.75vw;
    font-family: 'Barlow Condensed';
}
.resume-left-col h5 {
    font-size: 1.75vw;
    font-weight: bold;
    padding-bottom: 1vh;
}